import React from 'react'
import styled from 'styled-components'

const Div = styled.div`
  grid-row: 1;
  grid-column: 1;
`
const Input = styled.input`
  display: inline-flex;
  cursor: pointer;
  position: relative;
  width: 50px;
  height: 25px;
`

const hourOptions = [
  { value: '1:00', label: '1' },
  { value: '2', label: '2' },
  { value: '3', label: '3' },
  { value: '4', label: '4' },
  { value: '5', label: '5' },
  { value: '6', label: '6' },
  { value: '7', label: '7' },
  { value: '8', label: '8' },
  { value: '9', label: '9' },
  { value: '10', label: '10' },
  { value: '11', label: '11' },
  { value: '12', label: '12' },
]

const minOptions = [
  { value: '00', label: '00' },
  { value: '15', label: '15' },
  { value: '30', label: '30' },
  { value: '45', label: '45' },
]

const Timer = props => {
  return (
    <Div>
      {hourOptions.map(hour => (
        <Input
          key={hour.value}
          readOnly
          name="time"
          onClick={props.saveTime}
          value={hour.label}
        />
      ))}
      {minOptions.map(min => (
        <Input
          key={min.value}
          readOnly
          name="time"
          onClick={props.saveTime}
          value={min.label}
        />
      ))}
    </Div>
  )
}
export default Timer
